
.customers__bg-phone {
    position: absolute;
    z-index: -2;
    top: 0;
    left: 1px;
    width: 100%;
    height: 100%;
    @media screen and (min-width:480px) {
        display: none;
    }
}
.customers__bg-480 {
    position: absolute;
    display: none;
    z-index: -2;
    top: 0;
    left: 1px;
    width: 100%;
    height: 100%;
    @media screen and (min-width:480px)  {
        display: block;
    }
    @media screen and (min-width:768px)  {
        display: none;
    }
}
.customers__bg-tablet {
    position: absolute;
    display: none;
    z-index: -2;
    top: 0;
    left: 1px;
    width: 100%;
    height: 100%;
    @media screen and (min-width:768px)  {
        display: block;
    }
    @media screen and (min-width:1280px)  {
        display: none;
    }
}
.customers__bg-desk {
    position: absolute;
    display: none;
    z-index: -2;
    top: 0;
    left:1px;
    width: 100%;
    height: 100%;
    @media screen and (min-width:1280px)  {
        display: block;
    }
}


.customers__container {
    position: relative;
    display: flex;
    justify-content: center;
}




.customers {
    @extend %section-true;
    padding: 0px 20px 0px 20px;
    padding-bottom: 60px;
    padding-top: 60px;

    margin-left: auto;
    margin-right: auto;


    @media screen and (min-width:768px) and (max-width: 1280px) {
        padding: 0px 32px 0px 32px;
        padding-bottom: 120px;
        padding-top: 60px;  
    }
    @media screen and (min-width:1280px) {
        padding: 0px 16px 0px 16px;
        padding-bottom: 200px;
        padding-top: 100px;  
    }   
}

.customers__title {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 1.17;

    color: #2C2C2C;

    margin-bottom: 20px;

    @media screen and (min-width:768px) {
        font-weight: 700;
        font-size: 42px;
        line-height: 1.16;
       } 

}

.customers__text {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.17;
    display: inline-block;

    color: #2C2C2C;

    margin-bottom: 32px;

    @media screen and (min-width:768px){
        font-size: 16px;
        line-height: 1.18;
       } 
}

.customers__list {
   @media screen and (min-width:768px) {
    display: flex;
   } 
}

.customers__item {
    width: 100%;
    height: 247px;
    background-color: #FEFEFE;
    border-radius: 0px 30px 30px 30px;
    padding: 8px;

    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: column;

    @media screen and (max-width:767px) {
        &:last-child {
         height: 228px;
        }
    }

    @media screen and (min-width: 480px) and (max-width:768px) {
        //width: 440px;
    }
    @media screen and (min-width:1280px) {
        &:last-child {
        margin-left:139px;}
    }
    @media screen and (min-width:767px) and (max-width: 1280px) {
        &:last-child {
            margin-left: 32px;
        }
       width: 336px;
       height: 244px;
       margin-bottom: 0px;
       padding: 12px;
    }
    @media screen and (min-width:1280px){
       width: 501px;
       height: 227px;
       margin-bottom: 0px;
       padding: 14px;
    }
}

.customers__item:first-child {
        margin-bottom: 24px;
        @media screen and (min-width:767px) {
           margin-bottom: 0px;
        }
        
}

.customers__copyright {
    font-family: 'Roboto';
font-style: normal;
font-weight: 700;
font-size: 16px;
line-height: 1.18; 
margin: 0px;

color: #2C2C2C;


@media screen and (min-width:767px) and (max-width: 1280px) {
    font-size: 18px;
line-height: 1.16;
 }


color: #2C2C2C;

}

.customers__wrapper {
    border-right: 0.854167px solid #7EB101;
    display: flex;
    width: 162px;
    gap: 16px;
    align-items: center;

}

.customers__name {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;
    
    color: #2C2C2C;
}

.customers__description {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
color: #2C2C2C;
}

.customers__wraper-name {
    display: flex;
    flex-wrap: wrap;

}

.customers__icon-wrapper {
    display: flex;
    justify-content: wrap;
    gap: 4px;
}

.customers__element {
    width: 17px;
    height: 16px;
}

.customers__icon {
    width: 20px;
    height: 20px;
}

